.search {
  -webkit-animation: drop 0.8s;
          animation: drop 0.8s;
  transition: 0.8s ease-in-out;
}
@-webkit-keyframes drop {
  from {
    transform: translateY(-22%);
  }
  to {
    transform: translateY(0%);
  }
}
@keyframes drop {
  from {
    transform: translateY(-22%);
  }
  to {
    transform: translateY(0%);
  }
}

.search-overlay {
  -webkit-animation: view 0.5s;
          animation: view 0.5s;
}
@-webkit-keyframes view {
  from {
    opacity: 0.2;
  }
  to {
    opacity: 1;
  }
}
@keyframes view {
  from {
    opacity: 0.2;
  }
  to {
    opacity: 1;
  }
}

.detail-expand-cont {
  -webkit-clip-path: polygon(25% 50%, 75% 50%, 100% 100%, 0% 100%);
