.detail-bill {
  -webkit-animation: bill 0.5s;
          animation: bill 0.5s;
}
@-webkit-keyframes bill {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0%);
  }
}
@keyframes bill {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0%);
  }
}

.gala_hours::before {
	content: 'Offer Price';
	display: flex;
	width: 130px;
	height: 51px;
	background-color: #aef8a4;
	position: absolute;
	left: 0;
	top: 20px;
	justify-content: flex-start;
	align-items: center;
	color: #000000;
	font-size: 17px;
	font-weight: 600;
	padding-left: 6px;
}

.gala_hours::after {
	content: '';
	display: flex;
	width: 45px;
	height: 45px;
	background-color: #fefcef;
	position: absolute;
	left: 107px;
	top: 22px;
	justify-content: center;
	align-items: center;
	color: #000000;
	font-size: 17px;
	font-weight: 600;
	transform: rotate(43deg);
} /*# sourceMappingURL=detail.css.map */

